*{
  margin: 0;
  padding: 0;
}
body {
  margin: 10px;
  background-color: #e3eeee;;
}
.container{
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
}


table {
  display: inline-block;
  border-collapse: collapse;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  /* position: relative; */
 
}
ul{
  margin-left: 20px;
}


code{
  background-color: white;
  padding: 5px;
  color: rgba(5, 5, 4, 0.897);
}
pre {
  background: white;
  padding: 5px 0 5px 5px;
}
td,
th {
  border: 2px solid black;
  padding-left: 5px;
  padding-right: 5px;
}

textarea{
  width: 96%;
 min-height:1000px;
  
}

.editor{
  /* width: 100%; */
   
  flex: 1;
}

.preview {
  border:1px solid black;
  width: 98%;
  min-height:1000px;
  padding: 10px;
  flex: 1;
}


